import React from 'react';
import {BottomSheet} from 'react-spring-bottom-sheet';
import {BsPenFill} from 'react-icons/bs';
import {IoMdUndo,IoMdRedo} from 'react-icons/io'
import './Styles.css'
const Bs=({open,onDismiss})=>{
return(
<BottomSheet 
open={open}
onDismiss={onDismiss}
snapPoints={({ minHeight }) => minHeight}
>
<div className='InsideBottomSheet'>
    <div className="Button">
    <button> <h3>Pencil</h3></button>
    </div>
    <div className="Button">
    <button> <h3>Line</h3></button>
    </div>
    <div className="Button">
    <button><h3>Rectangle</h3></button>
    </div>
    <div className="Button">
    <button><h3>Circle</h3></button>
    </div> 
    <div className="Button">
    <button> <h3>Pan</h3></button>
    </div>
    <div className="Button">
    <button> <h3>Text</h3></button>
    </div>
</div>
</BottomSheet>
)}
export default Bs;