import React, { useState } from 'react';
import {SketchField, Tools} from 'react-sketch';

const SketchFieldDemo=(state)=> {
    const defaultSketchValue = {
        objects: [
          {
            type: 'circle',
            originX: 'left',
            originY: 'center',
            left: 284.91,
            top: 154.92,
            width: 57.32,
            height: 57.32,
            fill: 'transparent',
            stroke: '#000000',
            strokeWidth: 4,
            strokeDashArray: null,
            strokeLineCap: 'butt',
            strokeLineJoin: 'miter',
            strokeMiterLimit: 10,
            scaleX: 1,
            scaleY: 1,
            angle: 47.12,
            flipX: false,
            flipY: false,
            opacity: 1,
            shadow: null,
            visible: true,
            clipTo: null,
            backgroundColor: '',
            fillRule: 'nonzero',
            globalCompositeOperation: 'source-over',
            radius: 28.65783551172888,
            startAngle: 0,
            endAngle: 6.283185307179586
          },
          {
            type: 'rect',
            originX: 'left',
            originY: 'top',
            left: 374.91,
            top: 46.91,
            width: 14,
            height: 123,
            fill: '#00ff00',
            stroke: '#000000',
            strokeWidth: 4,
            strokeDashArray: null,
            strokeLineCap: 'butt',
            strokeLineJoin: 'miter',
            strokeMiterLimit: 10,
            scaleX: 1,
            scaleY: 1,
            angle: 0,
            flipX: false,
            flipY: false,
            opacity: 1,
            shadow: null,
            visible: true,
            clipTo: null,
            backgroundColor: '',
            fillRule: 'nonzero',
            globalCompositeOperation: 'source-over',
            rx: 0,
            ry: 0
          }
        ],
        background: '#ffffff'
      }
   
  const onChangeSketch = () => {
        console.log('change')
        // setState({now: Date.now()})
      }
        return (
            <div style={{background: '#000', padding: 10}}>
              <SketchField  
            // ref={sketch => (sketch = sketch)}
            onChange={onChangeSketch}
            width={state.sketchWidth}
            height={state.sketchHeight}
            tool={state.tool}
            lineColor={state.lineColor}
            lineWidth={state.lineWidth}
            fillColor={
              state.fillWithColor ? state.fillColor : 'transparent'
            }
            backgroundColor={state.backgroundColor}
            defaultValue={defaultSketchValue}
          />
            </div>
             
        )
}
export default SketchFieldDemo;
